import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import RegistroComponent from './components/RegistroComponent.vue'
import InicioSesion from './components/InicioSesion.vue'
import Pag_Principal from './components/Pag_Principal.vue'
import RegistroAsistencia from './components/RegistroAsistencia.vue'
import Historial_Asistencia from './components/Historial_Asistencia.vue'
import Consulta_Asistencia from './components/Consulta_Asistencia.vue'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = [
  {path: '/registro', component: RegistroComponent},
  {path: '/iniciar-sesion', component: InicioSesion},
  {path: '/principal', component: Pag_Principal},
  {path: '/asistencia', component: RegistroAsistencia},
  {path: '/historial_asistencia', name: 'historial_asistencia', component: Historial_Asistencia},
  {path: '/consulta_asistencia', component: Consulta_Asistencia},
]

const router = new VueRouter({
  routes
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
