<template>
  <div class="bordes-asistencia">
    <nav class="menu">
      <img :src="require('@/assets/logo.png')">
      <ul class="cuenta">
        <li><router-link to="/principal"> Inicio </router-link></li>
        <li class="separador">|</li>
        <li><router-link to="/iniciar-sesion"> Salir</router-link></li>
      </ul>
    </nav>
    <div class="contenedor-asistencia-imagen">
      <div class="contenedor-asistencia">
      <label>Profesores:</label>
      <div v-for="profesor in profesoresAsociados" :key="profesor">
        <div class="profesor-item">
          <input type="checkbox" :id="profesor" :value="profesor" v-model="profesoresSeleccionados" />
          <label :for="profesor">{{ profesor }}</label>
        </div>
      </div>
      <button @click="registrarAsistencia">Registrar Asistencia</button>
      </div>
    </div>
  </div>
</template>

<script>

import profesoresData from '@/profesores.json';

export default {
  data() {
    return {
      usuario: null,
      profesoresAsociados: [],
      profesoresSeleccionados: [],
    };
  },
  created() {
    this.usuario = this.$root.activeUser;
    if (this.usuario) {
      this.buscarProfesoresAsociados();
    } else {
      console.error('No se encontró un usuario activo');
    }
  },
  methods: {
    buscarProfesoresAsociados() {
      if (!this.usuario || !this.usuario.carrera) {
        console.error('No se ha proporcionado información de la carrera del usuario');
        return;
      }

      const { facultad, carrera } = this.usuario;
      console.log('Facultad y Carrera:', facultad, carrera);


      const profesoresAsociados = profesoresData.profesores.find(
        (p) => p.facultad === facultad && p.carrera === carrera
      );

      if (profesoresAsociados) {
        this.profesoresAsociados = profesoresAsociados.profesores;
      } else {
        console.error('No se encontraron profesores asociados a la facultad y carrera seleccionadas');
      }
    },
    registrarAsistencia() {
      const fechaHora = new Date();
      const fechaActual = new Date();
      const fechaFormateada = `${fechaActual.getFullYear()}-${(fechaActual.getMonth() + 1).toString().padStart(2, '0')}-${fechaActual.getDate().toString().padStart(2, '0')}`;
      const usuarioActivo = this.$root.activeUser;
      const asistenciaActual = this.profesoresAsociados
        .filter((profesor) => this.profesoresSeleccionados.includes(profesor))
        .map((profesor) => ({
          usuario: usuarioActivo,
          nombre: profesor,
          fecha: fechaFormateada,
          hora: fechaHora.toLocaleTimeString(),
          estado: 'Asistencia',
        }));

      const historialCompleto = JSON.parse(localStorage.getItem('historialAsistencia')) || [];

      const historialMasActual = [...historialCompleto, ...asistenciaActual];

      localStorage.setItem('historialAsistencia', JSON.stringify(historialMasActual));

      this.$router.replace('/historial_asistencia');
    }
  },
};
</script>

<style scoped>
.bordes-asistencia {
  width: 90%;
  margin: 0 auto;
  border-left: 50px solid #009200;
  border-right: 50px solid #ff0000;
  padding: 20px;
}

