  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
  background-color: #efefef;
}

.menu img {
  width: 150px;
  height: 100px;
}

.separador {
  margin: 0 10px;
  color: #999;
}

.menu ul {
  display: inline-block;
  padding: 15px 10px;
  list-style: none;
  background-color: #7ce175;
  border: 1px solid #797979;
  border-radius: 10px;
  font-size: 20px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}

.menu li {
  display: inline;
  margin-right: 10px;
}

.menu a {
  text-decoration: none;
  color: #000000;
}

.menu a:hover {
  text-decoration: underline;
}

.contenedor-asistencia-imagen {
  background-image: url('~@/assets/paraninfo.jpeg');
  padding: 100px;
    background-repeat: repeat;
    background-size: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contenedor-asistencia {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  border: 5px solid #797979;}


.profesor-item {
  display: flex;
