  margin-right: 10px;
}

.contenedor-asistencia label {
  margin-bottom: 8px;
}

.contenedor-asistencia button {
  margin-top: 10px;
  padding: 12px 24px;
  font-size: 18px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
