<template>
  <div class="bordes-historial">
    <nav class="menu">
      <img :src="require('@/assets/logo.png')">
      <ul class="cuenta">
        <li><router-link to="/principal"> Inicio </router-link></li>
        <li class="separador">|</li>
        <li><router-link to="/iniciar-sesion"> Salir</router-link></li>
      </ul>
    </nav>
    <div class="contenedor-historial">
      <h1>HISTORIAL DE ASISTENCIAS</h1>
      <table>
        <thead>
          <tr>
            <th>Docente</th>
            <th>Fecha</th>
            <th>Hora</th>
            <th>Asistencia</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(registro, index) in obtenerHistorialAsistencia()" :key="index">
            <td>{{ registro.nombre }}</td>
            <td>{{ registro.fecha }}</td>
            <td>{{ registro.hora}}</td>
            <td
              :class="{ 'asistencia-verde': registro.estado === 'Asistencia', 'inasistencia-rojo': registro.estado !== 'Asistencia' }">
              {{ registro.estado }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>


<script>
export default {
  methods: {
    obtenerHistorialAsistencia() {
      const usuarioActivo = this.$root.activeUser;
      const historialCompleto = JSON.parse(localStorage.getItem('historialAsistencia')) || [];
      const historialFiltrado = historialCompleto.filter(registro => registro.usuario.id === usuarioActivo.id && registro.usuario.carrera === usuarioActivo.carrera);
      console.log('Historial de asistencia filtrado:', historialFiltrado);
      return historialFiltrado;
    },
  },

  mounted() {
    console.log('Componente Historial_Asistencia montado');
  },
};
</script>

<style>
.bordes-historial {
  width: 90%;
  margin: 0 auto;
  border-left: 50px solid #009200;
  border-right: 50px solid #ff0000;
  padding: 20px;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
  background-color: #efefef;
}

.menu img {
  width: 150px;
  height: 100px;
}

.separador {
  margin: 0 10px;
  color: #999;
}

.menu ul {
  display: inline-block;
  padding: 15px 10px;
  list-style: none;
  background-color: #7ce175;
  border: 1px solid #797979;
  border-radius: 10px;
  font-size: 20px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}

.menu li {
  display: inline;
  margin-right: 10px;
}

.menu a {
  text-decoration: none;
  color: #000000;
}

.menu a:hover {
  text-decoration: underline;
}

.contenedor-historial {
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.contenedor-historial h1 {
  font-size: 28px;
  color: #333;
}

.contenedor-historial table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.contenedor-historial table,
th,
td {
  border: 1px solid #ccc;
}

.contenedor-historial th,
td {
  padding: 10px;
  text-align: center;
}

.contenedor-historial thead {
  background-color: #333;
  color: #fff;
}

.contenedor-historial tbody tr:nth-child(even) {
  background-color: #f5f5f5;
}

.asistencia-verde {
  color: green;
}

.inasistencia-rojo {
  color: red;
}
</style>