<template>
  <div class="bordes-registro">
  <div class="registro-principal">
    <h1>Registro de Usuario</h1>
    <div class="registro-form">
      <form @submit.prevent="submitRegistro" class="registro-form-contenedor">
        <div class="registro-form-group">

          <label for="registro_name">Nombre:</label>
          <input type="text" id="registro_name" v-model="registro_name"><br>
          <label for="registro_apellido">Apellido:</label>
          <input type="text" id="registro_apellido" v-model="registro_apellido"><br>
          <label for="registro_email">Correo Electrónico:</label>
          <input type="email" id="registro_email" v-model="registro_email"><br>
          <label for="registro_telefono">#Telefono:</label>
          <input type="text" id="registro_telefono" v-model="registro_telefono"><br>
          <label for="facultad">Facultad:</label>
          <select id="facultad" v-model="registro_facultad">
            <option disabled value="">Escoja una facultad</option>
            <option value="v1">FACULTAD CIENCIAS DE LA SALUD</option>
            <option value="v2">FACULTAD CIENCIAS ADMINISTRATIVAS, CONTABLES Y COMERCIO</option>
            <option value="v3">FACULTAD DE EDUCACIÓN TURISMO ARTES Y HUMANIDADES</option>
            <option value="v4">FACULTAD INGENIERÍA, INDUSTRIA Y CONSTRUCCIÓN</option>
            <option value="v5">FACULTAD CIENCIAS DE LA VIDA Y TECNOLOGÍAS</option>
            <option value="v6">FACULTAD CIENCIAS SOCIALES DERECHO Y BIENESTAR</option>
          </select>
          <br>
          <label for="carreras">Carrera:</label>
          <select id="carreras" v-model="registro_carrera">
            <option value="default">Selecciona una facultad primero</option>
            <option v-for="carrera in carrerasPorFacultad[registro_facultad]" :key="carrera">{{ carrera }}</option>
          </select>
          <br>
          <label for="registro_password">Contraseña:</label>
          <input type="password" id="registro_password" v-model="registro_password"><br>
          <label for="registro_comprobarpwd">Comprobar Contraseña</label>
          <input type="password" id="registro_comprobarpwd" v-model="registro_comprobarpwd"><br>
          <button type="submit">Registrarse</button>
          <p>¿Ya tienes una cuenta? <router-link to="/iniciar-sesion">Inicia Sesión</router-link></p>
        </div>
      </form>
    </div>
  </div>
  </div>
</template>
  
<script>
import profesoresData from "@/profesores.json";

export default {
  data() {
    return {
      registro_name: '',
      registro_apellido: '',
      registro_email: '',
      registro_telefono: '',
      registro_password: '',
      registro_comprobarpwd: '',
      registro_facultad: '',
      registro_carrera: 'default',
      carrerasPorFacultad: {
        v1: ["Medicina", "Odontología", "Enfermería", "Fisioterapia", "Fonoaudiología", "Laboratorio Clínico", "Terapia Ocupacional", "Psicología"],
        v2: ["Administración de Empresas", "Mercadotecnia", "Contabilidad y Auditoría", "Auditoría y Control de Gestión", "Finanzas", "Comercio Exterior", "Gestión de la Información Gerencial"],
        v3: ["Educación Inicial", "Educación Especial", "Psicología Educativa", "Educación Básica", "Pedagogía de la Actividad Física y el Deporte", "Pedagogía de la Lengua y la Literatura", "Pedagogía de los Idiomas Nacionales y Extranjeros", "Turismo", "Hospitalidad y Hotelería", "Artes Plásticas", "Sociología"],
        v4: ["Ingeniería Civil", "Ingeniería Marítima", "Electricidad", "Arquitectura", "Ingeniería Industrial", "Ingeniería de Alimentos"],
        v5: ["Ingeniería Agropecuaria", "Agronegocios", "Ingeniería Agroindustrial", "Ingeniería Ambiental", "Ingeniería en Tecnologías de la Información", "Ingeniería en Software", "Ingeniería en Sistema", "Biología"],
        v6: ["Derecho", "Economía", "Trabajo Social", "Comunicación"]
      },
      errorMessages: [],
    };
  },
  methods: {
    submitRegistro() {
      this.errorMessages = [];
      if (!this.registro_name || !this.registro_apellido || !this.registro_email || !this.registro_telefono || !this.registro_password || !this.registro_comprobarpwd || this.registro_facultad === "" || this.registro_carrera === "default") {
        window.alert('Por favor, complete todos los campos.');
        return;
      }

      if (!/^[a-zA-ZñÑáéíóúÁÉÍÓÚüÜ\s]+$/.test(this.registro_name) || !/^[a-zA-ZñÑáéíóúÁÉÍÓÚüÜ\s]+$/.test(this.registro_apellido)) {
        window.alert('Los nombres y apellidos no deben contener números.');
        return;
      }
      
      if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.registro_email)) {
        window.alert('El correo electrónico no tiene el formato correcto.');
        return;
      }

      if (isNaN(this.registro_telefono) || this.registro_telefono.length !== 10) {
        window.alert('El teléfono debe contener solo números y tener una longitud de 10 dígitos.');
        return;
      }

      if (this.registro_password !== this.registro_comprobarpwd) {
        window.alert('Las contraseñas no coinciden.');
        return;
      }
  
      const usuarioRegistrado = {
        name: this.registro_name,
        apellido: this.registro_apellido,
        email: this.registro_email,
        telefono: this.registro_telefono,
        password: this.registro_password,
        facultad: this.registro_facultad,
        carrera: this.registro_carrera,
        profesoresAsociados: [],

      };

      const profesoresAsociados = profesoresData.profesores.find(
    (p) => p.facultad === usuarioRegistrado.facultad && p.carrera === usuarioRegistrado.carrera
  );

  if (profesoresAsociados) {
    usuarioRegistrado.profesoresAsociados = profesoresAsociados.profesores;
  }
      const usuariosRegistrados = JSON.parse(localStorage.getItem('usuariosRegistrados')) || [];
      usuariosRegistrados.push(usuarioRegistrado);
      localStorage.setItem('usuariosRegistrados', JSON.stringify(usuariosRegistrados));
      window.alert('Registro exitoso');
      this.$router.push('/iniciar-sesion');
    },
  },
};
</script>

  
<style scoped>
.bordes-registro{
    width: 90%;
    margin: 0 auto;
    border-left: 50px solid #009200;
    border-right: 50px solid #ff0000;
    padding: 20px;
}

.registro-principal {
  background-image: url('~@/assets/estatua.jpg');
  display: flex;
    padding: 100px;
    background-repeat: no-repeat;
    background-size: 100%;
}

.registro-principal h1 {
  font-size: 90px;
    font-family: Georgia, Times, 'Times New Roman', serif;
    color: #ffffff;
    -webkit-text-stroke: 2px #000000;
    -webkit-text-fill-color: rgb(255, 255, 255);
    align-self: center;
}

.registro-form {
  padding: 30px;
    border: 2px solid #878787;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 1, 0, 0.2);
    background-color: rgba(255, 255, 255, 0.8);
}

.registro-form-group label {
  display: block;
  margin-bottom: 5px;
  text-align: left;
  font-weight: bolder;
}

.registro-form-group input{
  width: 300px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    background-color: #ffffff;
}
.registro-form-group select {
  width: 310px;
    height: 50px;
    margin: 10px auto;
}

.registro-form-group button {
  width: 150px;
    height: 40px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    font-family: monospace;
    background:#cbffcb;
    display: block;
    margin: 2px auto;
}
</style>