<template>
  <div class="login-bordes">  
    <div class="login-principal">
        <div class="login-form">
            <h2>Iniciar Sesion</h2>
            <form class="login-form-contenedor">
                <div class="login-form-group">
                    <label for="log_email">Correo Electrónico:</label>
                    <input type="email" id="log_email" v-model="log_email" required>
                </div>
                <div class="login-form-group">
                    <label for="log_password">Contraseña:</label>
                    <input type="password" id="log_password" v-model="log_password" required>
                    <p>¿No tienes una cuenta? 
                      <router-link to ="/registro">Registrate</router-link>
                    </p>
                </div>
                <button type="button" @click="submitLog">Ingresar</button>
            </form>
        </div>
    </div>
  </div>
</template>
  
<script>
export default {
  data() {
    return {
      log_email: '',
      log_password: '',
      errorMessages: []
    };
  },
  methods: {
    submitLog() {

      const usuariosRegistrados = JSON.parse(localStorage.getItem('usuariosRegistrados')) || [];
      const usuarioEncontrado = usuariosRegistrados.find(user => user.email === this.log_email);

      if (!usuarioEncontrado){
        window.alert('No se ha encontrado un usuario con ese correo. ¡Registrate!');
        return;
      }

      if (usuarioEncontrado.password !== this.log_password){
        window.alert('Contraseña incorrecta. Intentalo de nuevo');
        return;
      }
      this.$root.activeUser = usuarioEncontrado;
      this.$router.push('/principal');
    }
  },
};
</script>

  
<style scoped>

.login-bordes{
  width: 90%;
    margin: 0 auto;
    padding: 20px;
    border-left: 50px solid #009200;
    border-right: 50px solid #ff0000;
}
.login-principal {
  background-image: url('~@/assets/plaza.jpg');
  background-size: cover;
  background-position: center;
  height: 120vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-form {
  padding: 20px;
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px);
    border: 5px solid #797979;
}

.login-form h2 {
  text-align: center;
  color: #333;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 40px;
}

.login-form-contenedor label {
  display: block;
  margin: 10px 0;
  color: #333;
  font-size: 18px;
}

.login-form-group input[type="email"],
.login-form-group input[type="password"] {
  width: 250px;
    height: 10px;
    padding: 10px;
    margin: 10px;
}

.login-form-contenedor button {
  width: 100%;
  padding: 10px;
  background-color: #5500ff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  font-size: 18px;
}

.login-form-contenedor button:hover {
  background-color: #6b00b3;
}
</style>