<template>
  <div class="bordes-consulta">
    <nav class="menu">
      <img :src="require('@/assets/logo.png')">
      <ul class="cuenta">
        <li><router-link to="/principal">Inicio</router-link></li>
        <li class="separador">|</li>
        <li><router-link to="/iniciar-sesion">Salir</router-link></li>
      </ul>
    </nav>
    <div class="contenedor-consulta">
      <h1>Consulta de Asistencias</h1>
      <div class="filtro-fecha">
        <label for="fecha">Selecciona la fecha:</label>
        <input type="date" id="fecha" v-model="fechaSeleccionada">
        <button @click="consultarAsistencias">Consultar</button>
      </div>
      <table>
        <thead>
          <tr>
            <th>Docente</th>
            <th>Fecha</th>
            <th>Hora</th>
            <th>Asistencia</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(registro, index) in asistenciasFiltradas" :key="index">
            <td>{{ registro.nombre }}</td>
            <td>{{ registro.fecha }}</td>
            <td>{{ registro.hora }}</td>
            <td
              :class="{ 'asistencia-verde': registro.estado === 'Asistencia', 'inasistencia-rojo': registro.estado !== 'Asistencia' }">
              {{ registro.estado }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fechaSeleccionada: '',
      asistenciasFiltradas: [],
    };
  },
  methods: {
    consultarAsistencias() {
      console.log('Método consultarAsistencias llamado');
      const historialCompleto = this.obtenerHistorialAsistencia();
      console.log('Fecha seleccionada:', this.fechaSeleccionada);

      const fechaFinDia = new Date(this.fechaSeleccionada);
      fechaFinDia.setHours(23, 59, 59, 999);

      const usuarioActivo = this.$root.activeUser;
      this.asistenciasFiltradas = historialCompleto.filter((asistencia) => {
        return (
          asistencia.fecha === fechaFinDia.toISOString().split('T')[0] &&
          asistencia.usuario.id === usuarioActivo.id &&
          asistencia.usuario.carrera === usuarioActivo.carrera
        );
      });

      console.log('Asistencias filtradas:', this.asistenciasFiltradas);
      console.log('Funciona consultar asistencias');
    },
  formatearFecha(fecha) {
    const fechaFormateada = new Date(fecha);
    return `${fechaFormateada.getDate()}/${fechaFormateada.getMonth() + 1}/${fechaFormateada.getFullYear()}`;
  },
  obtenerHistorialAsistencia() {
    const historialCompleto = JSON.parse(localStorage.getItem('historialAsistencia')) || [];
    return historialCompleto;
  },
  },
  mounted() {
    console.log('Componente Consulta_Asistencia montado');
  },
};
</script>

<style scoped>
.bordes-consulta {
  width: 90%;
  margin: 0 auto;
  border-left: 50px solid #009200;
  border-right: 50px solid #ff0000;
  padding: 20px;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
  background-color: #efefef;
}

.menu img {
  width: 150px;
  height: 100px;
}

.separador {
  margin: 0 10px;
  color: #999;
}

.menu ul {
  display: inline-block;
  padding: 15px 10px;
  list-style: none;
  background-color: #7ce175;
  border: 1px solid #797979;
  border-radius: 10px;
  font-size: 20px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
}

.menu li {
  display: inline;
  margin-right: 10px;
}

.menu a {
  text-decoration: none;
  color: #000000;
}

.menu a:hover {
  text-decoration: underline;
}
.contenedor-consulta {
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.contenedor-consulta h1 {
  font-size: 28px;
  color: #333;
}

.contenedor-consulta table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.contenedor-consulta table,
th,
td {
  border: 1px solid #ccc;
}

.contenedor-consulta th,
td {
  padding: 10px;
  text-align: center;
}

.contenedor-consulta thead {
  background-color: #333;
  color: #fff;
}

.contenedor-consulta tbody tr:nth-child(even) {
  background-color: #f5f5f5;
}

.asistencia-verde {
  color: green;
}

.inasistencia-rojo {
  color: red;
}

input[type="date"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

input[type="date"]:focus {
  outline: none;
  border-color: #007bff; 
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

input[type="date"]:active,
input[type="date"]:hover {
  border-color: #0056b3; 
}

input[type="date"]::placeholder {
  color: #aaa;
}

input[type="date"]:not(:placeholder-shown) {
  color: #333;
}

button {
  padding: 10px 20px;
  margin: 2px;
  font-size: 16px;
  background-color: #4caf50; 
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #45a049; 
}


button:active {
  background-color: #367e3a; 
}

label {
  font-size: 16px;
  color: #333; 
  margin-bottom: 5px;
  display: block;
}

input[type="date"]:focus + label {
  color: #007bff;
}

input[type="date"]:not(:placeholder-shown) + label {
  color: #007bff;
}

input[type="date"]:hover + label {
  color: #0056b3;
}

</style>